.left-nav {
  width: 200px;
  height: 100%;
  overflow: auto;
  background-color: #e6f3fd;
  padding: 10px 0;
  transition: all 0.5s; }
  [data-theme='default'] .left-nav {
    background-color: #e6f3fd; }
  [data-theme='deongaree'] .left-nav {
    background-color: #344058; }
  [data-theme='green'] .left-nav {
    background-color: #009688; }
  [data-theme='red'] .left-nav {
    background-color: #ffffff; }
  .left-nav.left-nav-close {
    width: 50px; }
  .left-nav .left-nav-item {
    line-height: 40px; }
    .left-nav .left-nav-item .item-link {
      width: 100%;
      height: 40px;
      justify-content: space-between;
      padding: 0 15px;
      border-left: 4px solid transparent; }
      .left-nav .left-nav-item .item-link.open .suffix-icon {
        transform: rotate(180deg); }
      .left-nav .left-nav-item .item-link i {
        font-size: 14px; }
      .left-nav .left-nav-item .item-link .prefix-icon {
        margin-right: 10px; }
      .left-nav .left-nav-item .item-link .suffix-icon {
        transition: all 0.5s; }
      .left-nav .left-nav-item .item-link .right {
        flex: 1;
        justify-content: space-between; }
    .left-nav .left-nav-item .left-nav-item-children {
      transition: all 0.5s;
      overflow: hidden;
      background-color: #dde9f3; }
      [data-theme='default'] .left-nav .left-nav-item .left-nav-item-children {
        background-color: #dde9f3; }
      [data-theme='deongaree'] .left-nav .left-nav-item .left-nav-item-children {
        background-color: #313b4f; }
      [data-theme='green'] .left-nav .left-nav-item .left-nav-item-children {
        background-color: #02766c; }
      [data-theme='red'] .left-nav .left-nav-item .left-nav-item-children {
        background-color: #ffffff; }
      .left-nav .left-nav-item .left-nav-item-children .left-nav-item-children-item .item-link {
        width: 100%;
        height: 100%;
        justify-content: start;
        padding-left: 50px; }
        .left-nav .left-nav-item .left-nav-item-children .left-nav-item-children-item .item-link .prefix-icon {
          font-size: 12px; }
    .left-nav .left-nav-item .item-link {
      font-size: 12px;
      color: #333333;
      cursor: pointer; }
      [data-theme='default'] .left-nav .left-nav-item .item-link {
        color: #333333; }
      [data-theme='deongaree'] .left-nav .left-nav-item .item-link {
        color: #b5b5b5 !important; }
      [data-theme='green'] .left-nav .left-nav-item .item-link {
        color: #ffffff !important; }
      [data-theme='red'] .left-nav .left-nav-item .item-link {
        color: #333333 !important; }
      .left-nav .left-nav-item .item-link:hover {
        border-left: 4px solid rgba(51, 51, 51, 0.73) !important;
        background: linear-gradient(to left, dodgerblue, darkturquoise) !important;
        color: #ffffff; }
        [data-theme='default'] .left-nav .left-nav-item .item-link:hover {
          background: linear-gradient(to left, dodgerblue, darkturquoise) !important;
          border-color: rgba(51, 51, 51, 0.73) !important; }
        [data-theme='deongaree'] .left-nav .left-nav-item .item-link:hover {
          background: #252d3d !important;
          border-color: #6f7b80 !important; }
        [data-theme='green'] .left-nav .left-nav-item .item-link:hover {
          background: #02b7a5 !important;
          border-color: #2bf8e5 !important; }
        [data-theme='red'] .left-nav .left-nav-item .item-link:hover {
          background: rgba(204, 0, 0, 0.77) !important;
          border-color: #830303 !important; }
    .left-nav .left-nav-item .selected {
      border-left: 4px solid rgba(51, 51, 51, 0.73) !important;
      background: linear-gradient(to left, dodgerblue, darkturquoise) !important;
      color: #ffffff !important; }
      [data-theme='default'] .left-nav .left-nav-item .selected {
        background: linear-gradient(to left, dodgerblue, darkturquoise) !important;
        border-color: rgba(51, 51, 51, 0.73) !important; }
      [data-theme='deongaree'] .left-nav .left-nav-item .selected {
        background: #252d3d !important;
        border-color: #6f7b80 !important; }
      [data-theme='green'] .left-nav .left-nav-item .selected {
        background: #02b7a5 !important;
        border-color: #2bf8e5 !important; }
      [data-theme='red'] .left-nav .left-nav-item .selected {
        background: rgba(204, 0, 0, 0.77) !important;
        border-color: #830303 !important; }
  .left-nav .popover-content-render .ant-popover-arrow {
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: rgba(0, 0, 0, 0.75);
    border-left-color: rgba(0, 0, 0, 0.75); }
  .left-nav .popover-content-render .ant-popover-inner {
    background: rgba(0, 0, 0, 0.75);
    border-color: rgba(0, 0, 0, 0.75);
    color: #ffffff; }
    .left-nav .popover-content-render .ant-popover-inner a {
      color: #ffffff;
      font-size: 12px; }
      .left-nav .popover-content-render .ant-popover-inner a:hover {
        color: #1E9FFF; }
  .left-nav .popover-content-render .popover-container {
    line-height: 30px; }
